<template>
    <div class="table">
        <img-view v-if="showImg" :imgSrc="imgSrc" @clickit="showImg=false"></img-view>
        <div class="container">
            <div class="handle-box">
                <el-form :inline="true" label-width="80px">
                    <el-button @click="bannerAdd">添加轮播图</el-button>
                </el-form>
            </div>
            <el-table :data="dataList" border class="table" ref="multipleTable">
                <el-table-column label="轮播图" min-width="100">
                    <template slot-scope="scope">
                        <div class="goods-info">
                            <div class="goods-img-wrap">
                                <img
                                    class="goods-img"
                                    :src="scope.row.bannerImg"
                                    @click="viewImg(scope.row.bannerImg)"
                                    alt
                                >
                            </div>
                        </div>
                    </template>
                </el-table-column>

                <el-table-column label="操作时间" min-width="80">
                    <template slot-scope="scope">
                        <p v-if="scope.row.createTime">创建时间:{{scope.row.createTime}}</p>
                        <p v-if="scope.row.onSaleTime">上架时间:{{scope.row.onSaleTime}}</p>
                        <p v-if="scope.row.downSaleTime">下架时间:{{scope.row.downSaleTime}}</p>
                        <p v-if="scope.row.delTime">删除时间:{{scope.row.delTime}}</p>
                    </template>
                </el-table-column>

                <el-table-column label="操作" min-width="80">
                    <template slot-scope="scope">
                        <el-button
                            type="text"
                            icon="el-icon-sort-up"
                            @click="handleGoods('up',scope.row)"
                        >升序</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-sort-down"
                            @click="handleGoods('down',scope.row)"
                        >降序</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-edit"
                            @click="handleGoods('edit', scope.row)"
                        >编辑</el-button>
                        <el-button
                            type="text"
                            icon="el-icon-delete"
                            class="red"
                            v-if="scope.row.status!=3"
                            @click="handleGoods('del', scope.row)"
                        >删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </div>
        
    </div>
</template>

<script>
import ImgView from "../../components/common/imageView.vue";
export default {
    name: "bannerList",
    components: {
        ImgView
    },
    data() {
        return {
            dataList: [], //列表数据
            parames: {
                roleId: 0
            }, //请求参数
            imgSrc: null, //预览图片地址
            showImg: false //显示预览组件
        };
    },
    created() {
        this.getDataList();
    },

    activated() {
        if (this.$route.params.onload) {
            if (this.$route.params.onloadDeep) {
                this.parames = {
                    roleId: 0
                };
            }
            this.getDataList();
        }
    },
    methods: {
        async getDataList() {
            const data = await this.$api.BannerList(this.parames);
            this.dataList = data;
        },
        //预览
        viewImg(src) {
            this.showImg = true;
            // 获取当前图片地址
            this.imgSrc = src;
        },

        //添加轮播图
        bannerAdd() {
            this.$router.push("/bannerAdd");
        },

        //轮播图处理
        handleGoods(status, row) {
            if (status === "up") {
                this.bannerRank(row.bannerId, 1);
            } else if (status === "down") {
                this.bannerRank(row.bannerId, 2);
            } else if (status === "del") {
                this.$confirm("删除该轮播图, 是否继续?", "提示", {
                    confirmButtonText: "确定",
                    cancelButtonText: "取消",
                    type: "warning"
                })
                    .then(() => {
                        this.bannerDel(row.bannerId);
                    })
                    .catch(() => {
                        this.$message({
                            type: "info",
                            message: "已取消删除"
                        });
                    });
            } else if (status === "edit") {
                let obj = {
                    bannerId:row.bannerId,
                    bannerImg:row.bannerImg,
                    actionUrl:row.actionUrl
                }
                this.$router.push(`/bannerEdit?bannerDetail=${JSON.stringify(obj)}`);
            }
        },
        //轮播图排序
        async bannerRank(bannerId, sortOrder) {
            const data = await this.$api.BannerRank(
                { bannerId, sortOrder },
                bannerId
            );
            this.getDataList();
        },
        //轮播图删除
        async bannerDel(bannerId) {
            const data = await this.$api.BannerDelete({ bannerId }, bannerId);
            this.$message({
                message: "删除成功",
                type: "success"
            });
            this.getDataList();
        }
    }
};
</script>

<style scoped>
.handle-box {
    margin-bottom: 20px;
}

.handle-select {
    width: 120px;
}

.handle-input {
    width: 200px;
    display: inline-block;
}
.del-dialog-cnt {
    font-size: 16px;
    text-align: center;
}
.table {
    width: 100%;
    font-size: 14px;
}
.mr10 {
    margin-right: 10px;
}
.red {
    color: #ff0000;
}
img{
    cursor:pointer;
}
</style>
